@charset "UTF-8";
body {
  margin: 0;
  padding: 0;
  color: #0e0e0e;
  min-width: 320px;
}

html {
  box-sizing: border-box;
  overflow-y: scroll;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding: 0;
}

.container {
  max-width: 1160px;
  margin: 0 auto;
}

@media (max-width: 1295px) {
  .container {
    max-width: 90%;
  }
}
@media (max-width: 1180px) {
  body.active {
    overflow: hidden;
    height: 100vh;
  }
}
.header {
  width: 100%;
  height: 80px;
  background-color: #b11733;
  position: relative;
  z-index: 1000;
}
.header .header__wrapper {
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header .header__wrapper .header__logo {
  width: 75px;
  height: 62px;
  cursor: pointer;
}
.header .header__wrapper .header__logo svg {
  transition: 0.2s;
}
.header .header__wrapper .header__logo:hover svg {
  opacity: 0.7;
}
.header .header__wrapper .header__nav {
  width: 627px;
}
.header .header__wrapper .header__nav ul {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.header .header__wrapper .header__nav ul li {
  list-style: none;
}
.header .header__wrapper .header__nav ul li a {
  font-family: Georgia;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 23px;
  text-align: center;
  text-transform: uppercase;
  color: #ffffff;
  text-decoration: none;
  transition: 0.2s;
}
.header .header__wrapper .header__nav ul li a:hover {
  font-family: "Georgia";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 23px;
  text-transform: uppercase;
  color: #e3cb85;
  opacity: 0.8;
}
.header .header__wrapper .header__nav ul li a:active {
  opacity: 0.5;
}
.header .header__wrapper .header__phone a {
  font-family: "Georgia";
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 23px;
  text-align: center;
  color: #ffffff;
  text-decoration: none;
  transition: 0.2s;
}
.header .header__wrapper .header__phone a:hover {
  color: #cfb368;
}

@media (max-width: 1180px) {
  .header .header__wrapper {
    padding: 0 15px;
  }
  .header .header__wrapper .header__logo {
    position: relative;
    z-index: 1000;
  }
  .header .header__wrapper .header__phone {
    position: relative;
    z-index: 1000;
  }
  .header .header__wrapper .header__nav {
    position: fixed;
    top: -1000px;
    left: 0;
    z-index: 500;
    background-color: #b11733;
    transition: 0.7s;
    width: 100%;
    height: 100%;
  }
  .header .header__wrapper .header__nav ul {
    flex-direction: column;
    width: 100%;
    opacity: 0;
    margin-top: 150px;
  }
  .header .header__wrapper .header__nav ul li {
    width: 100%;
    text-align: center;
    margin-bottom: 30px;
  }
  .header .header__wrapper .header__nav.active {
    top: 0;
  }
  .header .header__wrapper .header__nav.active ul {
    opacity: 1;
  }
}
@media (max-width: 621px) {
  .header .header__wrapper .header__phone a {
    font-size: 16px;
  }
}
.swiper {
  width: 100%;
  height: calc(100vh - 160px);
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;
  /* Center slide text vertically */
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  color: #fff;
}

.swiper-slide__data {
  width: 100%;
  height: 100%;
}
.swiper-slide__data .swiper-slide__top-block {
