  display: flex;
  font-family: "Georgia";
  font-style: normal;
  font-weight: 700;
  font-size: 25px;
  line-height: 28px;
  text-transform: uppercase;
  color: #ffffff;
  align-items: center;
  margin-bottom: 5px;
}
.swiper-slide__data .swiper-slide__top-block .swiper-slide__top-block-date .swiper-slide__date .swiper-slide__date-information .swiper-slide__date-information-day {
